@import url(https://fonts.googleapis.com/css?family=PT+Sans);
.mainPage {
  background-image: url(/clothing-site/static/media/titlepic.57930e3a.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 620px;
  margin: auto 50px;
}

@media only screen and (max-width: 1200px) {
  .mainPage {
    height: 500px;
    margin: 0;
    width: 100%;
  }
}

@media only screen and (min-width: 600px) and (max-width: 900px) {
  .mainPage {
    background-image: url(/clothing-site/static/media/cropped2.27d6be56.png);
    /* width: 100%; */
    margin: 0;
    padding: 0;
    background-size: 100% 100%;
  }
}

@media only screen and (max-width: 600px) {
  .mainPage {
    background-image: url(/clothing-site/static/media/cropped.3b7a7d50.png);
    /* width: 100%; */
    margin: 0;
    background-size: 100% 100%;
  }
}
.logo {
  height: 70px;
  width: 70px;
  margin-left: 25px;
}

.dropdown {
  margin: auto 75px;
  text-align: center;
  display: inline-block;
}

.cart {
  margin: auto 10px;
}

.banner {
  background-color: black;
  color: white;
  height: 50px;
  text-align: center;
  font-size: 1.4em;
  line-height: 50px;
}

.card {
  height: 300px;
  width: 400px;
  margin-top: 10px;
  display: inline-block;
  text-align: center;
}

.card img {
  height: 100%;
  width: 100%;
}
.text-wrapper {
  /* width: 100%; */
  /* position: relative; */
  margin-top: -50%;
  color: white;
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

