@import url("https://fonts.googleapis.com/css?family=PT+Sans");

.mainPage {
  background-image: url("../images/titlepic.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 620px;
  margin: auto 50px;
}

@media only screen and (max-width: 1200px) {
  .mainPage {
    height: 500px;
    margin: 0;
    width: 100%;
  }
}

@media only screen and (min-width: 600px) and (max-width: 900px) {
  .mainPage {
    background-image: url("../images/cropped2.png");
    /* width: 100%; */
    margin: 0;
    padding: 0;
    background-size: 100% 100%;
  }
}

@media only screen and (max-width: 600px) {
  .mainPage {
    background-image: url("../images/cropped.png");
    /* width: 100%; */
    margin: 0;
    background-size: 100% 100%;
  }
}
.logo {
  height: 70px;
  width: 70px;
  margin-left: 25px;
}

.dropdown {
  margin: auto 75px;
  text-align: center;
  display: inline-block;
}

.cart {
  margin: auto 10px;
}

.banner {
  background-color: black;
  color: white;
  height: 50px;
  text-align: center;
  font-size: 1.4em;
  line-height: 50px;
}

.card {
  height: 300px;
  width: 400px;
  margin-top: 10px;
  display: inline-block;
  text-align: center;
}

.card img {
  height: 100%;
  width: 100%;
}
.text-wrapper {
  /* width: 100%; */
  /* position: relative; */
  margin-top: -50%;
  color: white;
  text-align: center;
}
